.scorePage {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: center;
    padding: 32px 64px;
    &__mainContent {
        align-items: center;
        justify-content: center;
        flex-direction: column;
        display: flex;
        gap: 64px;
        width: 60%;
        height: 580px;
    }
    &__score {
        position: relative;
        @include display-flex();
        @include justify-content(center);
        @include align-items(baseline);
    }
    &__list, .categoryButtonsList {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        gap: 16px;
        width: 20%;
    }
    &__text {
            text-align: right;
            text-transform: uppercase;
            padding-bottom: 16px;
            opacity: 0;
            transition: opacity 0.5s ease; 
            @extend %fontSize--m;
        &--visible {
            opacity: 1;
        }
    }
}

@media (max-width: $break-l) {
    .scorePage {
        padding: 32px;
        gap: 48px;
        &__mainContent {      
            height: auto;
            width: 100%;
            gap: 48px;
        }
        &__list, .categoryButtonsList {
            width: 100%;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: center;
            .scorePage__text {
                width: 100%;
                text-align: center;
                font-size: 20px;
            }
        }
    }
}

@media (max-width: $break-s) {
    .scorePage {
        flex-direction: column;
        padding: 24px 16px;
        height: auto;
        &__mainContent {
            width: 100%;
            height: auto;
            gap: 32px;
        }
        &__list, .categoryButtonsList {
            width: 100%;
            max-width: 206px;
            padding: 0;
            align-items: flex-start;
            justify-content: flex-start;
        }
        &__text {
            margin: $s-medium 0 0;
            text-align: center;
            font-size: 14px;
        }
    }
}